import styled from 'styled-components';

export const Title = styled.div`
  font-size: 24px;
  font-weight: bold;
  text-align: center;
  margin: 70px auto;
  position: relative;
  display: block;
  max-width: 300px;


  &:after {
    content: "";
    position: absolute;
    left: 10%;
    bottom: -10px;
    border-bottom: 5px solid black;
    width: 80%;
  }
`;

export const Container = styled.div`
  max-width: 1000px;
  margin: 0 auto;
  width: 100%;
`;


