import React, { PureComponent } from 'react';
import styled from 'styled-components';
import profileImage from './cris.jpg';
import Skill from './Skill';
import { Title } from '../../components';

const Container = styled.div`
  max-width: calc(1000px - 40px);
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  padding: 20px;
  background: rgba(0,0,0,0.03);

  @media (min-width: 900px) {
    flex-direction: row;
  }
`;

const WhoIs = styled.div`
  flex: 1;
  display: flex;
  flex-direction: column;
  text-align: center;
`;

const Skills = styled.div`
  flex: 1;
`;

const TitleWho = styled.div`
  font-size: 22px;
  font-weight: 600;
  margin: 20px 0;
`;

const DescriptionWho = styled.div`
  font-size: 18px;
  max-width: 80%;
  margin: 0 auto 30px;
`;

const ProfileImage = styled.img`
  width: 200px;
  margin: 0 auto;
`;

class AboutSkills extends PureComponent {
  render() {
    return (
      <div>
        <Title>ACERCA DE MI</Title>
        <Container>
          <WhoIs>
            <ProfileImage src={profileImage} />
            <TitleWho>¿Quien es Cristian?</TitleWho>
            <DescriptionWho>
              Soy un desarrollador Front-end, apasionado de los efectos visuales, animaciones tecnologías realtime y todo lo que tenga que ver con crear experiencias de usuario increíbles.
            </DescriptionWho>
          </WhoIs>
          <Skills>
            <Skill name="React" percent={80} />
            <Skill name="CSS" percent={80} />
            <Skill name="Python" percent={50} />
            <Skill name="React N." percent={80} />
            <Skill name="Javascript" percent={80} />
            <Skill name="Illustrator" percent={40} />
            <Skill name="Node" percent={60} />
            <Skill name="PHP" percent={60} />
            <Skill name="Devops" percent={60} />
            <Skill name="Angular" percent={50} />
            <Skill name="Git" percent={80} />
          </Skills>
        </Container>
      </div>
    );
  }

}

export default AboutSkills;


