import React, { PureComponent } from 'react';
import styled, { css, keyframes } from 'styled-components';

const appear = keyframes`
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
`;

const Image = styled.div`
  overflow: hidden;
  height: 0;
  padding-top: 60%;
  background-position: 0 50%;
  background-repeat: no-repeat;
  width: 100%;
  background-size: cover;
  ${props => props.isIntersecting ? css`
    background-image: url("${props => props.background}");
  ` : css`
    background: lightgray;
  `};
`;

const Container = styled.div`
  margin: 10px;
  padding: 10px;
  background: rgba(0,0,0,0.03);
  width: 100%;

  ${props => props.isIntersecting ? css`
    animation: ${appear} 0.5s;
  ` : css`
    opacity: 0;
  `}

  @media (min-width: 500px) {
    width: calc(50% - 40px);
  }
`;

const Url = styled.a`
  color: #D32F2F;
  font-weight: 600;
  font-size: 14px;
`;

const Stack = styled.div`
  font-size: 18px;
  color: #D32F2F;
  font-weight: 600;
`;

const Name = styled.div`
  font-size: 14px;
  color: #D32F2F;
  margin-top: 10px;
`;

class Project extends PureComponent {
  state = {}

  componentDidMount() {
    const options = {
      rootMargin: '0px',
      threshold: 1.0,
    }
    
    const observer = new IntersectionObserver(this.handleIntersection, options);
    observer.observe(this.containerRef);
  }

  handleIntersection = ([entry], observer) => {
    if (entry.isIntersecting) {
      this.setState({
        isIntersecting: entry.isIntersecting
      });
      observer.unobserve(this.containerRef);
    }
  }

  props: {
    name: string,
    url: string,
    stack: string,
    img: string,
  }

  render() {
    return (
      <Container
        innerRef={ref => { this.containerRef = ref }}
        isIntersecting={this.state.isIntersecting}
      >
        <Image
          background={this.props.img}
          isIntersecting={this.state.isIntersecting}
        />
        <Stack>{this.props.stack}</Stack>
        <Url href={this.props.url}>{this.props.url}</Url>
        <Name>{this.props.name}</Name>
      </Container>
    );
  }
}

export default Project;



