import React, { PureComponent } from 'react';
import styled from 'styled-components';
import Feature from './Feature';

const Container = styled.div`
  max-width: 1000px;
  margin: 0 auto;
  display: flex;
  width: 100%;
  clear: both;
  flex: 1;
  justify-content: center;
  flex-wrap: wrap;
`;

const SocialIcon = styled.svg`
  width: 40px;
  height: 40px;
  fill: white;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateY(-50%) translateX(-50%);
`;

class AboutFeatures extends PureComponent {
  state = {}

  componentDidMount() {
    const options = {
      rootMargin: '0px',
      threshold: 1.0,
    }
    
    const observer = new IntersectionObserver(this.handleIntersection, options);
    observer.observe(this.containerRef);    
  }

  handleIntersection = ([entry], observer) => {
    if (entry.isIntersecting) {
      this.setState({
        isIntersecting: entry.isIntersecting
      });
      observer.unobserve(this.containerRef);
    }
  }

  render() {
    return (
      <Container innerRef={ref => { this.containerRef = ref }}>
        <Feature
          isIntersecting={this.state.isIntersecting}
          order={1}
          icon={(
            <SocialIcon className="icon">
              <use xlinkHref="#speed" />
            </SocialIcon>
          )}
          title="Páginas rápidas"
          desc="Me gustan los tiempos de carga cortos, interacciones libres de lag, tiempo = dinero"
        />
        <Feature
          isIntersecting={this.state.isIntersecting}
          order={2}
          icon={(
            <SocialIcon className="icon">
              <use xlinkHref="#responsive-symbol-with-a-widescreen-monitor-a-cellphone-and-a-tablet" />
            </SocialIcon>
          )}
          title="Responsive"
          desc="Siempre busco que mis layouts se adapten en cualquier dispositivo, grandes y pequeños"
        />
        <Feature
          isIntersecting={this.state.isIntersecting}
          order={3}
          icon={(
            <SocialIcon className="icon">
              <use xlinkHref="#test" />
            </SocialIcon>
          )}
          title="TDD, BDD"
          desc="Trabajo con metodologías que faciliten realizar cambios en el código sin generar bugs."
        />
        <Feature
          isIntersecting={this.state.isIntersecting}
          order={4}
          icon={(
            <SocialIcon className="icon">
              <use xlinkHref="#glasses" />
            </SocialIcon>
          )}
          title="Código facíl de leer"
          desc="El código se lee mas veces de las que se escribe, por eso siempre busco que sea fácil de leer"
        />
      </Container>
    );
  }
}

export default AboutFeatures;



