import React, { PureComponent } from 'react';
import styled, { keyframes, css } from 'styled-components';

const rotate = keyframes`
  0% {
    transform: scaleX(-1);
  }

  100% {
    transform: scaleX(1);
  }
`;

const Hexagon = styled.div`
  position: relative;
  width: 100px; 
  height: 57.74px;
  background-color: #ec4942;
  margin: 28.87px 0;
  will-change: transform;
  ${props => props.isIntersecting && css`
    animation: ${rotate} ${props => (props.order * 0.4)}s;
  `}

  &:before,
  &:after {
    content: "";
    position: absolute;
    width: 0;
    border-left: 50px solid transparent;
    border-right: 50px solid transparent;
  }

  &:before {
    bottom: 100%;
    border-bottom: 28.87px solid #ec4942;
  }

  &:after {
    top: 100%;
    width: 0;
    border-top: 28.87px solid #ec4942;
  }
`;

const Title = styled.div`
  font-weight: 500;
  font-size: 18px;
  margin: 10px 0;
`;

const Description = styled.div`
  font-size: 14px;
  max-width: 230px;
`;

const Icon = styled.i`
  position: absolute;
  top: 50%;
  left: 50%;
  translate: -50%;
`;

const Container = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 10px;
  margin-bottom: 30px;
  width: 230px;
`;

class Feature extends PureComponent {
  props: {
    icon: React$Element<any>,
    title: string,
    desc: string,
    isIntersecting: boolean,
    order: number,
  }

  render() {
    return (
      <Container>
        <Hexagon order={this.props.order} isIntersecting={this.props.isIntersecting}>
          {this.props.icon}
        </Hexagon>
        <Title>{this.props.title}</Title>
        <Description>{this.props.desc}</Description>
      </Container>
    );
  }
}

export default Feature;



