import React, { PureComponent } from 'react';
import styled from 'styled-components';

const Container = styled.div`
  width: 100%;
  display: flex;
  margin-bottom: 10px;
`;

const Label = styled.div`
  width: 100px;
  background: #ec4942;
  text-align: center;
  line-height: 30px;
  color: white;
`;

const Percent = styled.div`
  width: 100%;
  background: lightgray;
  background-size: 100% 30%;
  min-height: 30px;
  line-height: 30px;
  text-align: right;
  padding-right: 10px;
  position: relative;
`;

const Bar = styled.div`
  width: ${props => props.value}%;
  transition: all 1s;
  background: #D32F2F;
  height: 100%;
  display: inline-block;
  position: absolute;
  left: 0;
`;

class Skill extends PureComponent {
  state = {}

  componentDidMount() {
    const options = {
      rootMargin: '0px',
      threshold: 1.0,
    }
    
    const observer = new IntersectionObserver(this.handleIntersection, options);
    observer.observe(this.containerRef);
    
  }

  handleIntersection = ([entry], observer) => {
    if (entry.isIntersecting) {
      this.setState({
        isIntersecting: entry.isIntersecting
      });
      observer.unobserve(this.containerRef);
    }
  }

  props: {
    name: string,
    percent: number,
  }

  render() {
    const { name, percent } = this.props;
    return (
      <Container innerRef={ref => { this.containerRef = ref }}>
        <Label>{name}</Label>
        <Percent>
          <Bar value={this.state.isIntersecting ? percent : 0} />
          {percent}%
        </Percent>
      </Container>
    );
  }
}

export default Skill;



