import React from 'react';
import styled from 'styled-components';
import { Title, Container } from '../../components';
import Project from './Project';
import verinmueblesPNG from './screenshoots/verinmuebles.png'
import inmoblexPNG from './screenshoots/inmoblex.png'
import cfmcPNG from './screenshoots/cfmc.png'
import casasSabanaPNG from './screenshoots/casas_sabana.png'
import chatbotPNG from './screenshoots/chatbot.png'
import appPNG from './screenshoots/app.png'

const ProjectsContainer = styled.div`
  width: 100%;
  display: flex;
  flex-wrap: wrap;
`;

function Projects() {
  return (
    <Container>
      <Title>Algunos de mis Proyectos</Title>
      <ProjectsContainer>
        <Project
          name="inmoblex"
          url="https://inmoblex.com"
          stack="PWA, Webpack, React, Node SSR, API PHP yii"
          img={inmoblexPNG}
        />
        <Project
          name="Página web cfmc"
          url="http://cfmc.com.co"
          stack="Webpack, React, API PHP Yii, Firebase"
          img={cfmcPNG}
        />
        <Project
          name="Landing Verinmuebles"
          url="https://verinmuebles.com"
          stack="Browserify, Angular, Jquery, API PHP Yii"
          img={verinmueblesPNG}
        />
        <Project
          name="Chatbot Inmoblex - Chatbot para vender un inmueble o para requerir un inmueble"
          url="https://inmoblex.com/requerimiento"
          stack="Watson, Firebase"
          img={chatbotPNG}
        />
        <Project
          name="Página web Casas en la sabana"
          url="http://casasenlasabana.com/"
          stack="Vanilla JS, API PHP Yii"
          img={casasSabanaPNG}
        />
        <Project
          name="MVP Aplicación Inmoblex - No disponible al publico contactar para demo"
          url="Disponible en Repo privado"
          stack="React Native"
          img={appPNG}
        />
      </ProjectsContainer>
    </Container>
  );
}

export default Projects;



