import React, { PureComponent } from 'react';
import styled from 'styled-components';

const Container = styled.div`
  background: #111a22;
  overflow: hidden;
  padding-top: 100vh;
  position: relative;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
`;

const BackgroundCanvas = styled.canvas`
  bottom: 0;
  height: 100%;
  left: 0;
  right: 0;
  top: 0;
  position: absolute;
  width: 100%;
`;

const Title = styled.h1`
  font-size: 32px;
  font-weight: 400;
  color: white;
  text-align: center;
  line-height: 1.2em;
  margin: 0;
`;

const Subtitle = styled.h2`
  font-size: 32px;
  font-weight: 400;
  color: white;
  text-align: center;
  line-height: 1.2em;
  margin: 0;
`;

const Content = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
  width: 100%;
  height: 100%;
`;

const Name = styled.span`
  color: #e74c3c;
`;

const Button = styled.a`
  font-size: 20px;
  color: white;
  padding: 10px 20px;
  border: 2px solid white;
  margin-top: 20px;
  transition: all 0.2s;
  text-decoration: none;
  cursor: pointer;

  &:hover {
    background: #e74c3c;
    border: 2px solid #e74c3c;
  }
`;

class Hero extends PureComponent {
  componentDidMount() {
    this.startBackgroundAnimation();
  }
  
  componentWillUnmount() {
    if (this.interval) {
      clearInterval(this.interval);
    }
  }

  generateStar = () => {
    const canvas = this.canvasRef;
    const ctx = canvas.getContext('2d');
    const starRadius = 3;
    ctx.beginPath();
    ctx.arc(starRadius + (Math.random() * canvas.width), starRadius + (Math.random() * canvas.height), starRadius*Math.random(), 0, Math.PI*2, false);
  
    var rand = Math.random();
    if(rand <= 0.5){
        ctx.fillStyle = "rgba(255, 255, 255, 1)";
        ctx.shadowColor = "rgba(255, 255, 255, 0.5)";
        ctx.shadowBlur = 3;
    }
    else if(rand > 0.75){
        ctx.fillStyle = "rgba(255, 254, 196, 1)";
        ctx.shadowColor = "rgba(255, 254, 196, 0.5)";
        ctx.shadowBlur = 4; 
    }
    else{
        ctx.fillStyle = "rgba(192, 247, 255, 1)";
        ctx.shadowColor = "rgba(192, 247, 255, 0.5)";
        ctx.shadowBlur = 7;
    }
    ctx.fill();
  }

  startBackgroundAnimation = () => {
    const ctx = this.canvasRef.getContext('2d');
    this.interval = setInterval(this.generateStar, 5);
    setTimeout(() => {
      clearInterval(this.interval);
    }, 1000);
    this.onresize();
  }

  onresize = () => {
    this.canvasRef.width = this.canvasRef.clientWidth;
    this.canvasRef.height = this.canvasRef.clientHeight;
  }

  handleClick = (event) => {
    document.getElementById('my-work').scrollIntoView({
      behavior: "smooth",
      block:    "start",
    });
  }

  canvasRef: HTMLCanvasElement;

  render() {
    return (
      <Container>
        <BackgroundCanvas
          innerRef={(ref) => { this.canvasRef = ref; }}
          width="100%"
          height="100%"
        />
        <Content>
          <Title>Hola, Soy <Name>Cristian Cedeño</Name></Title>
          <Subtitle>Soy desarrollador front-end</Subtitle>
          <Button onClick={this.handleClick}>Conocer mi trabajo</Button>
        </Content>
      </Container>
    );
  }
}

export default Hero;



